<template>
  <section class='battle--center'>
    <!--TEAMMATES-->
    <p class='battle__heading'>Teammates</p>
    <div class='battle'>
      <div
        v-for='(pokemon, key) in pokemon.team'
        class='battle__item'
        :key='key'
        @click='updatePokemon(key)'
      >
        <div> 
          <img :src='`/2d/${key}.png`' />
        </div>
      </div>
    </div>

    <!--CHECKS AND COUNTERS-->
    <p class='battle__heading'>Checks and Counters</p>
    <div class='battle' v-if='pokemon.weak'>
      <div
        v-for='(pokemon, key) in pokemon.weak'
        class='battle__item'
        :key='key'
        @click='updatePokemon(key)'
      >
        <div>
          <img :src='`/2d/${key}.png`' />
        </div>
      </div>
    </div>
    <div v-else class='battle battle--center'>
      <p>No data</p>
    </div>

    <!--METAGAME THREATS-->
    <p class='battle__heading'>Strong against</p>
    <div class='battle' v-if='pokemon.strong'>
      <div
        v-for='(pokemon, key) in pokemon.strong'
        class='battle__item'
        :key='key'
        @click='updatePokemon(key)'
      >
        <div>
          <img :src='`/2d/${key}.png`' />
        </div>
      </div>
    </div>
    <div v-else class='battle battle--center'>
      <p>No data</p>
    </div>
  </section>
</template>

<script>
import axios from "axios";

export default {
  props: ["data"],
  data() {
    return {
      pokemon: this.data,
    };
  },
  methods: {
    updatePokemon(pokemon) {
      const tier = this.pokemon.tier;
      if (!this.isMobile) {
        axios
          .get(`/pokemon/${tier}/${pokemon}`)
          .then(res => {
            this.$store.commit("dex/SET_SELECTED_POKEMON", res.data.pokemon);
          })
          .catch(err => alert(err));
          
      } else {
        this.$router.push(`/dex/${tier}/${pokemon}`)
      }
    }
  },
  watch: {
    data: function(n) {
      this.pokemon = n;
    }
  }
};
</script>