<template>
  <div class="bs">
    <table class="bs-table">
      <tr>
        <th>Pokemon</th>
        <th>Item</th>
        <th>Nature</th>
        <th>
          <p>Spread</p>
          <p class="bs-labels" v-for="(stat, key) in statLabels" :key="key" :class="`${stat}-font`">{{stat}}</p>
        </th>
        <th>Move</th>
        <th>Maximum damage</th>
      </tr>
      <tr v-for="(battle, key) in battleCalculator" :key="key">
        <td><img :src="'/2d/'+battle.attacker+'.png'"></td>
        <td><img :src="'/items/'+battle.item+'.png'"></td>
        <td>{{battle.nature}}</td>
        <td>
          <p class="bs-spread" :class="`${statLabels[key]}-font`" v-for="(stat, key) in battle.spread" :key="key">
            {{stat}} 
          </p>
        </td>
        <td>{{battle.move}}</td>
        <td>{{battle.damageTaken}}%</td>
      </tr>
    </table>
  </div>
</template>

<script>
import axios from "axios"

import components from "./script/components"
import computed from "./script/computed"
import methods from "./script/methods"
import watchers from "./script/watchers"

export default {
  props:['pokemon'],
  data() {
    return {
      fetchedData: null,
      statLabels:['hp', 'atk', 'def', 'spa', 'spd', 'spe']
    };
  },
  components,
  computed,
  methods,
  watchers,
  created(){
    axios.post("/mysquad/retrieve", { keys: Object.keys(this.pokemon.weak), tier: this.$route.params.tier })
        .then((res) => {
            this.fetchedData = res.data.pokemon
        })
        .catch(err => err)
  }
};
</script>

<style lang="scss">
    @import "./style";
</style>